<template lang="pug">
    .new__teacher-content
        .teacher__title Преподаватель
        form(@submit.prevent='saveTeacher').new__teacher
            .new__teacher-points
                .form__row
                    .form__item
                        img(src='../../../assets/images/icons/butterfly4.svg').img
                    .form__item
                        label.label__name Имя
                            input(type='text' v-model='teacher.firstName').new__teacher__name
                    .form__item
                        label.label__name Фамилия
                            input(type='text' v-model='teacher.lastName').new__teacher__lastname
                    .form__item
                        label.label__name ИИН
                            input(type='text' v-model='teacher.username').new__teacher__iin
                .form__row
                    .form__item
                        label.label__name  Образование
                            select(v-model="teacher.education").new__teacher__education
                                option(value='') Выбрать
                                option(:value='item' v-for='item in educationLevels') {{item}}
                    .form__item
                        label.label__name  Департамент 
                            select(v-model="teacher.teacherDepartment.id").new__teacher__department
                                option(value=0) Выбрать
                                option(:value='item.id' v-for='item in departments') {{item.name}}
                    .form__item
                        label.label__name Опыт работы
                            input(type='number' v-model="teacher.experience").new__teacher__experience
                .form__row
                    button(type='submit').save__button-teacher Сохранить
                    button(@click.prevent='resetTeacher').reset__button-teacher Отмена
            
</template>
<script>
import { mapActions } from "vuex";
export default {
    data() {
        return {
            teacher: {
                firstName: '',
                lastName: '',
                username: '',
                experience: 0,
                password: 'qwerty',
                education: '',
                teacherDepartment: {
                    id: 0                
                }
            },
            educationLevels: [
                'Высшее', 'Магистратура', 'Докторантура', 'Ученая степень'
            ]      
        }
    },
    props: {
        departments: Array
    },
    methods: {
        ...mapActions("teachers", ["addTeacher"]),
        ...mapActions("error", ["fetchError"]),
        saveTeacher() {
            if(this.teacher.firstName=='' || 
            this.teacher.lastName=='' || 
            this.teacher.username=='' ||
            this.teacher.experience==0 ||
            this.teacher.education=='' ||
            this.teacher.teacherDepartment.id==0
            ) {
                this.fetchError("Не все поля заполнены!")
            }
            else {
                this.addTeacher(this.teacher)
                this.$emit('eventNewTeacher')
            }
        },
        resetTeacher() {
            this.$emit('eventNewTeacher')
        }
    }
}
</script>
